import React from 'react';
import { Image, Spinner } from 'react-bootstrap';
import './Profile.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

const baseurl = "https://raw.githubusercontent.com/";
const profileurl = `${baseurl}${process.env.REACT_APP_USERNAME}/${process.env.REACT_APP_USERNAME}/main/static/profile.json`;

class Profile extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            item: {}
        };
    }

    componentDidMount() {
        fetch(profileurl)
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        item: result
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            )
    }

    createContactInformation(item) {
        return (<p> <FontAwesomeIcon className="contact-font" icon={item.faIcon} aria-hidden="true" />
            <a href={item.url} target={item.target} rel="noreferrer">{item.displayName} </a> </p>);
    }

    render() {
        const profile = this.props.dataParentToChild;
        const { isLoaded, item, error } = this.state;
        if (error) {
            return (<div>Error: Unable to fetch details from github APIs {error.message}</div>);
        }
        else if (isLoaded) {
            return (
                <div>
                    <Image className="profile-picture" roundedCircle fluid src={profile.avatar_url} />
                    <br />
                    <div className="details">
                        <h2> {profile.name} </h2>
                        <p> {profile.bio} </p>
                    </div>
                    <div className="contact-details">
                        <p> <FontAwesomeIcon className="contact-font" icon="map-marker-alt" aria-hidden="true" /> {profile.location} </p>
                        {item.contact.map(contactInfo => this.createContactInformation(contactInfo))}
                    </div>
                </div>
            );
        } else {
            return (
                <div className="loading-box"><Spinner animation="grow" variant="primary" /></div>
            );
        }
    }
}

export default Profile;