import React from 'react';
import { Container, Col, Row, Spinner } from 'react-bootstrap';
import About from './About';
import Profile from './Profile'
import './Home.css';
import Posts from './Posts';

const baseurl = `https://api.github.com/users/`
const data = `${baseurl}${process.env.REACT_APP_USERNAME}`;

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: null,
      isLoaded: false,
      item: {}
    };
  }

  componentDidMount() {
    fetch(data)
      .then(res => res.json())
      .then(
        (result) => {
          this.setState({
            isLoaded: true,
            item: result
          });
        },
        (error) => {
          this.setState({
            isLoaded: true,
            error
          });
        }
      )
  }

  render() {
    const { isLoaded, error, item } = this.state;
    if (error) {
      return (<div>Error: Unable to fetch details from github APIs {error.message}</div>);
    }
    else if (isLoaded) {
      return (
        <Container id="Home" fluid className="home-container dark">
          <Row>
            <Col md={3} className="profile-box">
              <Profile dataParentToChild={item} />
            </Col>
            <Col md={9} className="data-box">
              <Row>
                <Col md={12}>
                  <About dataParentToChild={item} />
                </Col>
              </Row>
              <Row>
                <Col md={12}>
                  <Posts />
                </Col>
              </Row>
            </Col>
          </Row>
        </Container>
      );
    } else {
      return (
        <div className="loading-box"><Spinner animation="grow" variant="primary" /></div>
      );
    }
  }
}

export default Home;
