import React from 'react';
import { Card, Button, Col, Row, Spinner, Container } from 'react-bootstrap'
import './Posts.css'; //Change it according to new interface

const feedurl = `https://www.medium.com/feed/@${process.env.REACT_APP_USERNAME}`
const rsstojson = `https://api.rss2json.com/v1/api.json?rss_url=${feedurl}`;

class Posts extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            medium: {}
        };
    }

    componentDidMount() {
        fetch(rsstojson)
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        medium: result
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            )
    }

    createCard(story) {
        return (
            <Row key={story.pubdate} style={{ marginTop: '0.2rem', marginBottom: '0.2rem' }}>
                <Col sm={12}>
                    <Card className="h-100 post-card" bg="dark">
                        <Card.Body>
                            <Row>
                                <Col sm={4}><Card.Img className="post-image" src={story.thumbnail} alt="Card image" /></Col>
                                <Col sm={8}>
                                    <h4>{`${story.title} - ${new Date(story.pubDate).toDateString()}`}</h4>
                                    <br />
                                    <Button variant="primary" href={story.link} target="_blank">Go to story</Button>
                                </Col>
                            </Row>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        )
    }

    createnewcard(story) {
        return (
            <Col className="h-100 post-card" lg={4} md={6} sm={6} >
                <Card className="text-center" bg="dark" flex>
                    <Card.Img variant="top" src={story.thumbnail} style={{ opacity: 0.4 }} />
                    <Card.ImgOverlay className="d-flex flex-column justify-content-center">
                        <Card.Title><h3>{story.title}</h3></Card.Title>
                        <Card.Text>
                            <h4>{new Date(story.pubDate).toDateString()}</h4>
                            <Button variant="dark" href={story.link} style={{ maxWidth: "40%" }} target="_blank">Read More</Button>
                        </Card.Text>
                    </Card.ImgOverlay>
                </Card>
            </Col>
        );
    }

    render() {
        const { error, isLoaded, medium } = this.state;
        if (error) {
            return (<div>Error: Unable to fetch details from github APIs {error.message}</div>);
        } else if (!isLoaded) {
            return <div className="loading-box dark"><Spinner animation="grow" variant="primary" /></div>;
        } else {
            return (
                <Container fluid className="post-container" >
                    <Row className="post-heading">
                        <Col md={12}><h1>Posts</h1></Col>
                    </Row>
                    <Row className="post-box dark">
                        {medium.items.map(story => this.createnewcard(story))}
                    </Row>
                </Container>
            );
        }
    }
}

export default Posts;