import React from 'react';
import { Spinner } from 'react-bootstrap';
import "./About.css";

const baseurl = "https://raw.githubusercontent.com/";
const profileurl = `${baseurl}${process.env.REACT_APP_USERNAME}/${process.env.REACT_APP_USERNAME}/main/static/profile.json`;

class About extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            error: null,
            isLoaded: false,
            profile: {}
        };
    }

    componentDidMount() {
        fetch(profileurl)
            .then(res => res.json())
            .then(
                (result) => {
                    this.setState({
                        isLoaded: true,
                        profile: result
                    });
                },
                (error) => {
                    this.setState({
                        isLoaded: true,
                        error
                    });
                }
            )
    }

    render() {
        const { error, isLoaded, profile } = this.state;
        if (error) {
            return (<div>Error: Unable to fetch details from github APIs {error.message}</div>);
        } else if (!isLoaded) {
            return <div className="loading-box dark"><Spinner animation="grow" variant="primary" /></div>;
        } else {
            return (
              <div className="dark about-box">
              <h1>About Me</h1>
              {profile.about.paragraphs.map( para => <p>{para}</p> )}
              </div>
            );
        }
    }
}

export default About;